.page_header {
	@include fixed(0);
	z-index: 30;
	width: 100%;
	height: 65px;
	background-color: #000;

	> .inner {
		position: relative;
		height: 100%;
		display: flex;
		align-items: center;
	}

	.logo_block {
		display: block;
		@include wh(90, 35);

		@include media-breakpoint-down(llg) {
			margin: auto;
		}
	}


	&-nav {
		@include absolute(50%, 0);
		transform: translateY(-50%);

		ul {
			display: flex;
			align-items: center;
		}
		&-item {
			float: left;
			color: #fff;
			@include media-breakpoint-down(llg) {
				padding-right: 1rem;
			}

			&:not(:first-of-type) {
				padding-left: 30px;
				@include media-breakpoint-down(llg) {
					padding-left: 0;
				}
			}

			.icon_block {
				display: inline-block;
				vertical-align: middle;
				@include wh(20);
			}


			.icon-bag {
				position: relative;

				.bblens_badge {
					@include absolute(-11px, -11px);
					font-family: Century Gothic,CenturyGothic,AppleGothic,sans-serif;
					font-weight: bold;
				}
			}
		}
	}

	.mbr_info {
		display: inline-block;
		vertical-align: middle;
		// margin-left: 0.6rem;


		@include media-breakpoint-down(llg) {
			display: none;
		}

		&-item {
			padding: 0.3rem 0.8rem;

			&:not(:first-of-type) {
				position: relative;

				&:before {
					@include psudoClass();
					@include absolute(0, $L:0, $B:0);
					margin: auto;
					@include wh(1, 18);
					background-color: #fff;
				}
			}

			a {
				color: currentColor;

				@include hover_line(2, #fff);
			}
		}
	}
}