.bblens_popup-prescription, .bblens_popup-search_member {
	.bblens_popup-bd {
		border: none;
		padding-top: 0;
		padding-bottom: 0;
	}
}


.bblens_popup-search_member {
	.bblens_popup-wrap {
		width: 650px;
	}

	.row {
		margin-left: -0.5rem;
		margin-right: -0.5rem;
	}

	[class*="col-"] {
		padding-left: 0.5rem;
		padding-right: 0.5rem;
	}

	.bblens_frm-input {
		height: 50px;
	}


}


.member_status {
	padding-top: 3rem;
	padding-bottom: 3rem;

	&-p {
		display: flex;

		.title {
			flex: 0 0 130px;
			font-weight: bold;
		}

		.txt {
			font-weight: bold;
		}
	}
}


.member_status_btn {
	display: flex;
	justify-content: center;
}


.update_file {
	margin-bottom: 1.6rem;
	.bblens_popup-prescription_image & {
		display: flex;
		justify-content: center;
	}
	.dropzone {
		min-height: 270px;
		border: 3px dashed #9F9F9F;
		border-radius: 3px;

		display: flex;
		justify-content: center;
		align-items: center;
	}

	.dz-started {
		.dz-message {
			display: none;
		}
	}

	.dz-message {
		line-height: 3rem;
	}

	.dz-remove {
		@include absolute(-20px, -20px);
		@include circle(40);
		background-color: color("main");
		&:before {
			@include psudoClass;
			@include absolute(50%, $L:50%);
			transform: translate(-50%, -50%);
			@include wh(80%, 3);
			background-color: #fff;
		}
		&:after {
			@include psudoClass;
			@include absolute(50%, $L:50%);
			transform: translate(-50%, -50%) rotate(90deg);
			@include wh(80%, 3);
			background-color: #fff;
		}
	}
}

.dropzone .dz-preview .dz-error-message {
	width: 280px;

}