.bblens_frm-label {
	color: #000;
}

.bblens_frm-field {

}

.bblens_frm-input {
	border: none;
	width: 100%;
	height: 60px;
	border-radius: 5px;
	border: 1px solid #D1D1D1;
	background-color: #fff;
	padding-left: 0.8rem;
	padding-right: 0.8rem;
}


.bblens_frm-error {
	padding-top: 0.3rem;
	color: color("main");
}

.bblens_frm-select {
	position: relative;
	width: 100%;
	height: 60px;
	border-radius: 5px;
	border: 1px solid #D1D1D1;
	background-color: #fff;

	cursor: pointer;

	@include media-breakpoint-down(md) {
		height: 35px;
	}

	select {
		position: relative;
		z-index: 2;
		border: none;
		@include wh;
		background-color: transparent;
		padding-left: 0.8rem;
		padding-right: 0.8rem;
	}


	.arrow {
		@include psudoClass;
		@include absolute(calc(50% - 3px), 1rem);
		z-index: 1;
		@include wh(11, 6);
		background-image: url(../images/arrow2.svg);
		background-repeat: no-repeat;
		background-size: cover;

		transition: $transition-base;
	}

	&.js-open {
		.arrow {
			transform: scaleY(-1);
		}
	}
}


.bblens_frm-textarea {
	width: 100%;
	height: 135px;
	border-radius: 5px;
	border: 1px solid #D1D1D1;
	background-color: #fff;
	padding: 1rem;

	resize: none;
}