.bblens_popup-order_info {
	.bblens_popup-wrap {
		width: 800px;
	}

	.bblens_popup-bd {

		.strong {
			padding-left: 5px;
			padding-right: 5px;
			@include remCalc(font-size, 25);
			font-weight: bold;
		}

	}


	.order_info {
		@include remCalc(font-size, 20);
	}

	.order_info-row {
		display: flex;

		&:not(:first-of-type) {
			margin-top: 1.2rem;
		}
		.title {
			flex: 0 0 180px;
			letter-spacing: 5px;
		}
	}

}