.bblens_popup-add_cart {
	.bblens_popup-wrap {
		width: 620px;
	}
	.bblens_popup-hd {

		.svg_icon {
			display: block;
			@include wh(55);
			margin: 0 auto 1.2rem;
		}
	}
	.bblens_popup-bd {
		.txt {
			@include remCalc(font-size, 20);
		}

		.strong {
			padding-left: 5px;
			padding-right: 5px;
			@include remCalc(font-size, 25);
			font-weight: bold;
		}
	}

	.bblens_btn-contiunes {
		margin-right: 1.8rem;
	}
}