.#{$project_name} {
	&_btn {
		display: block;
		border: none;
		background-color: transparent;
		padding: 0.8rem 2.4rem;
		cursor: pointer;

		border-radius: 5px;

		transition: $transition-base;

		box-shadow: 0 0 0 rgba(#000, 0);
		color: #595757;
		text-align: center;

		&:hover {
			box-shadow: $boxShadowHover1;
		}

		.txt {
			margin-left: 0.6rem;
		}


		&-default {
			background-color: #E84B2E;
			color: #fff;

			&:hover {
				background-color: darken(#E84B2E, 10);
				color: #fff;
			}
		}

		&-orange {
			@include btn_setting(#D0482F, #fff);
		}

		&-blue {
			background-color: #1D2974;
			color: #fff;

			&:hover {
				background-color: lighten(#1D2974, 10);
				color: #fff;
			}
		}

		&-gray {
			background-color: #A8A7A7;
			color: #fff;

			&:hover {
				background-color: darken(#A8A7A7, 10);
				color: #fff;
			}
		}

