.body_wrap {
	min-height: 100vh;

}

.m_mask {
	@include wh;
	@include fixed(0, $L:0);
	z-index: 10;
	background-color: #fff;
	opacity: 0.5;
	display: none;
	.js-show-mmenu & {
		display: block;
	}
}

.page_wrap {
	display: block;
	&:not(.login_page) {
		@include absolute(0);
		@include wh;
		display: grid;
		grid-template-columns: 220px 1fr;
		grid-template-areas: "aside main";
		padding-top: 65px;

		@include media-breakpoint-down(llg) {
			display: block;
		}
	}



}

%openMmenu {
	transition: all 0.3s $easeInOutCubic3;
	.js-show-mmenu & {
		transform: translateX(220px);
	}
}

.page_header {
	@extend %openMmenu;
}

.page_aside {
	grid-area: aside;

	position: relative;
	z-index: 20;
	background-color: #000;
	padding: 2rem 10px;

	@extend %openMmenu;

	@include media-breakpoint-down(llg) {
		display: block;
		width: 220px;
		height: 100vh;

		@include fixed(0, $L:-220px);
	}
}

.page_main {
	grid-area: main;

	@extend %openMmenu;
}