.login_page {
	&.page_wrap {
		@include absolute(0);
		@include wh;
	}

	.page_main {

	}

	&-main {
		min-height: 100%;
		padding-top: 65px;
		display: flex;

		> .inner {
			display: grid;
		    grid-template-columns: 1fr 1fr;
		    grid-column-gap: 100px;
			align-items: center;

			@include media-breakpoint-down(llg) {
				display: block;
			}
		}
	}
}


.login_frm, .login_page-pic {
	flex: 1 1 50%;
}

.login_page-header {
	@include absolute(0);
	z-index: 2;
	width: 100%;
	height: 65px;
	background-color: #000;

	> .inner {
		height: 100%;
		display: flex;
		align-items: center;
	}

	.logo_block {
		display: block;
		@include wh(90, 35);
	}
}

.login_frm {
	@include media-breakpoint-down(llg) {
		margin-bottom: 3rem;
	}
	&-title {
		color: #3D3A39;
		font-size: 30px;
		letter-spacing: 1rem;

		text-align: center;
	}

	.validation_code {

		&-field {
			display: flex;
			align-items: center;

			@include media-breakpoint-down(sm) {
				flex-wrap: wrap;
			}
		}

		// .login_frm-input {
		// 	flex: 1 1 50%;
		// }

		&-input {
			flex: 1 1 auto;
			margin-right: 30px;

			@include media-breakpoint-down(sm) {
				flex: 1 1 100%;
				margin-right: 0;
				margin-bottom: 1rem;
			}
		}

		&-pic {
			flex: 0 0 150px;
			margin-right: 20px;
		}

		&-reset {
			flex: 0 0 40px;
			@include wh(40, 37);
			padding: 5px;

			background-color: rgba(#666, 0);
			border: none;
			border-radius: 3px;
			transition: background-color 0.3s ease-in-out;

			cursor: pointer;

			&:hover {
				background-color: rgba(#666, 0.1);
			}

			&:active {
				transform: translate(1px, 1px);
			}
		}
	}

	&-btns {
		padding-top: 10px;
	}

	&-submit {
		width: 100%;
	}
}